.cards {
  padding: 50px 40px;
  display: grid;
  gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
}

@media (min-width: 488px) {
  @include template-columns (2, 200);
}

@media (min-width: 768px) {
@include template-columns (3, 200);
}

@media (min-width: 1024px) {
  @include template-columns (4, 200);
}

.card{
  width: $card-width;
  height: $card-hight;
  border-radius: $radius;
  border: 1px solid #F3F3F3;
  transition-duration: 300ms;

&__image {
  display: block;
  width: 160px;
  height: 134px;
  padding: 32px 19px 40px;
}

&__information {
  margin: 0 16px 16px;
}

&__name {
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: $card-main-accent;
  margin: 0;
}

&:hover {
  transform: scale(1.2);
  & .card__name {
    color: $title-hover;
  }
}

&__code {
  color: $card-secondary-accent;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  margin-top: 4px;
  margin-bottom: 16px;
}

&__reviews {
  display: flex;
  justify-content: space-between;
  padding-bottom: 24px;
  align-self: center;

&-count {
  display: flex;
  color: $card-main-accent;
  margin: 0;
  line-height: 14px;
}
}

&__price {
  display: flex;
  justify-content: space-between;
  text-align: right;

&-title {
  font-family: inherit;
  color: $card-secondary-accent;
  font-size: 12px;
  line-height: 18px;
  margin: 0;
  }

&-value {
  font-family: inherit;
  color: $card-main-accent;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  margin: 0;
}
}

&__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  height: 40px;
  background-color: $card-blue-accent;
  border-radius: $radius;
  border-style: hidden;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase;
  color: $card-white-accent;
}

&__button:hover {
  background-color: $card-white-accent;
  color: $card-blue-accent;
  border: 1px solid $card-blue-accent;
}
}
