.cards {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  transition-duration: .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 32px 19px 40px;
  display: block;
}

.card__information {
  margin: 0 16px 16px;
}

.card__name {
  color: #060b35;
  margin: 0;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-self: center;
  padding-bottom: 24px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  margin: 0;
  line-height: 14px;
  display: flex;
}

.card__price {
  text-align: right;
  justify-content: space-between;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-family: inherit;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-style: hidden;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-of-type) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background: #fff;
  flex-direction: row;
  justify-content: space-between;
